<template>
        <lottie :options="defaultOptions" class="pospiskAnim" :height="400" :width="400" v-on:animCreated="handleAnimation"/>
</template>

<script>
import Lottie from '../lottie.vue'; 
import animationData from '../assets/pospisk.json';

   export default {
    name: 'app',
    components: {
      'lottie': Lottie
    },
    data() {
      return {
        defaultOptions: {
          animationData: animationData,
          autoplay: false,
          loop: false
        },
        animationSpeed: 1,
      }
    },
    methods: {
      handleAnimation: function (anim) { 
      setTimeout(startAnim, 1000);
      
      function startAnim() {
        this.anim = anim;
        anim.playSegments([0,160],true);
        // anim.playSegments([160,200],true);
      }
        //anim.goToAndStop(5, true);
      }
    },
    mounted: function () {
      //  this.$emit('wink');
    }
  }

</script>

