<template>
    <div class="heroHeader" 
            :style="{'background-image': 'url(' + require('../assets/webdev.jpg') + ')'}">
    <v-container fluid class='no_pd'>
        <v-layout class="heroWrap" wrap row fill-height>  
        <v-flex class="heroText" pa-5 ma-5 text-xs-left sm12 md4>
            <div class="heroTextInner">
              <h1 class="display-2 font-weight-black">Kristian Pospis</h1>
              <h2 class="font-weight-light font-italic">web developer &amp; designer</h2>
              <p class="py-3 body-1">I am <span class="imp"> {{ age }} years old </span> and I have <span class="imp">12 years of experience</span> using adobe <span class="imp">photoshop</span> also <span class="imp">7 years of experience</span> with adobe <span class="imp">Illustrator</span>. I specialize in <span class="imp">front-end</span> development and I have a passion for creating <span class="imp">interactive content</span>. I am currently <span class="imp">looking for a frontend position</span> in <span class="imp">Heide</span> (Schleswig Holstein), Germany. I have recently <span class="imp">graduated</span> from <span class="imp">Web Development</span> (BC) at UCL in Odense, Denmark. I have previously studied <span class="imp">Multimedia Design and Communication</span> (AP) at EAL (former UCL, 2015) in Odense.</p>
              <div class="skillChips">
                <v-tooltip top
                  v-for="(skill, i) in skills"
                  :key="i">
                  <template v-slot:activator="{ on }">
                      <v-chip small v-on="on">{{skill.title}}</v-chip>
                  </template>
                  <template>
                    <span>{{skill.info}}</span>
                  </template>
                </v-tooltip>
              </div>
              <span class="caption">hover for more info</span>
              
              <br>
              <v-btn  href="kpcv.pdf" target="_blank" class="nomg">CV(EN</v-btn>
              <v-btn  href="kpcvde.pdf" target="_blank">CV(DE)</v-btn>
              <v-btn  href="#/about">Timeline</v-btn>
            </div>
        </v-flex>
        <v-flex sm12 md8>
            <animPospisk class="screenHead" />
        </v-flex>              
        </v-layout>
    </v-container>
    </div>
</template>

<script>
import animPospisk from './animPospisk'

export default {
  name: 'App',
  components: {
    animPospisk
  },
  data: () => ({
    skills: [
        {
          title: 'PHP',
          info: 'WordPress (themes/plugins)',
        },
        {
          title: 'C# (C-Sharp)',
          info: 'ASP.NET, Umbraco, Xamarin & Xamarin.forms (crossplatform mobile development)',
        },
        {
          title: 'JavaScript',
          info: 'Vanilla, vueJs, NodeJs, jQuery, lottieJs (bodymovin), ',
        },
        {
          title: 'HTML/CSS',
          info: 'SASS/LESS'
        },
        {
          title: 'Adobe Photoshop',
          info: 'Web/App design, graphic design, mockups, compositions',
        },
        {
          title: 'Adobe Illustrator',
          info: 'Logos, Print, SVG illustrations & icons',
        },
        {
          title: 'Sketch App',
          info: 'Prototyping',
        },
        {
          title: 'Adobe After Effects',
          info: 'Videos, SFX, motion design, bodymovin, ',
        },
        {
          title: 'Languages',
          info: 'English, German, Hungarian, Slovak',
        },
    ],
      dob: '1996/06/25',
      age: 0,
        }),
  created: function () {
    var today = new Date();
    var dd = today.getDate();
    var mm = today.getMonth()+1; //January is 0!
    var yyyy = today.getFullYear();
    if(dd<10) {
    dd='0'+dd
    }
    if(mm<10) {
    mm='0'+mm
    }
    today = yyyy+'/'+mm+'/'+dd;

    var date2 = new Date(today);
    var date1 = new Date(this.dob);
    var timeDiff = Math.abs(date2.getTime() - date1.getTime());
    var dayDifference = Math.ceil(timeDiff / (1000 * 3600 * 24));
    this.age = Math.floor(dayDifference / 365);
  },
  mounted: function(){
    // variables
    const scrW = document.documentElement.clientWidth;
    const scrH = document.documentElement.clientHeight;

    const list = document.querySelector('.pospiskAnim svg');
    const children = list.childNodes;
    const grandChildren = children[1];
    const grandChild = grandChildren.childNodes;
    const groupEyes = grandChild[7];
    const groupEyesList = groupEyes.childNodes;
    
    const leftEyeX = groupEyesList[1]; // retina
    const leftEye = groupEyesList[2]; // pupil
    const leftX = groupEyesList[3]; // stop
    const rightEyeX = groupEyesList[6]; // retina
    const rightEye = groupEyesList[7]; // pupil
    const rightX = groupEyesList[8]; // stop
    var svgNS = "http://www.w3.org/2000/svg";  

    function animMotion (){

    const newDivL = document.createElementNS(svgNS,"g");
    newDivL.setAttributeNS(null, "class","leftGr");
    
    newDivL.appendChild(leftEyeX);
    newDivL.appendChild(leftEye);
  
    groupEyes.insertBefore(newDivL,leftX);

    const newDivR = document.createElementNS(svgNS,"g");
    newDivR.setAttributeNS(null, "class","rightGr");
    
    newDivR.appendChild(rightEyeX);
    newDivR.appendChild(rightEye);
  
    groupEyes.insertBefore(newDivR,rightX);

  }
    setTimeout(animMotion, 3500);


function handleOrientation(event) {
    const leftGrX = document.querySelector('.leftGr');
    const rightGrX = document.querySelector('.rightGr');
    var x = event.beta;  // In degree in the range [-180,180]
    var y = event.gamma; // In degree in the range [-90,90]

    // Because we don't want to have the device upside down
    // We constrain the x value to the range [-90,90]
    if (x >  90) { x =  90}
    if (x < -90) { x = -90}
    
    if (y >  90) { y =  -90}
    if (y < -90) { y = 90}
    
      var xN = x /10;
      var yN = y /10;
      var xF = xN.toFixed(2);
      var yF = yN.toFixed(2);

     leftGrX.setAttribute("transform","matrix(1,0,0,1,"+yF+","+xF+")");
     rightGrX.setAttribute("transform","matrix(1,0,0,1,"+yF+","+xF+")");
    }
    window.addEventListener('deviceorientation', handleOrientation);

    window.addEventListener('mousemove', function (e){

    setTimeout(animPos, 150);

    function animPos(){
      
        const leftGrX = document.querySelector('.leftGr');
        const rightGrX = document.querySelector('.rightGr');
    
        if(!leftGrX && !rightGrX){
            // console.log("not ready");
        }else{
            const mX = e.x; // pos
            const mY = e.y; // pos
            const nX = mX - scrW /2; // neg pos
            const nY = mY - scrH /2; // neg pos
            const nXc = nX / 150;
            const nYc = nY / 150;
            leftGrX.setAttribute("transform","matrix(1,0,0,1,"+nXc+","+nYc+")");
            rightGrX.setAttribute("transform","matrix(1,0,0,1,"+ nXc+","+ nYc+")");
            // console.log("nX = " + nX + " nY = " + nY+" | | nXc = " + nXc + "nYc = "+ nYc);
        }
    }
    });  
  }
  ,
  methods:{

  }
}
</script>

